<script lang="coffeescript">var blockClick, blocks, createRange, dispatch, interval;

import {
  createEventDispatcher
} from 'svelte';

export let calendarDate = Date.create();

export let events = [];

export let showColumnHeadings = false;

export let showRowHeadings = false;

dispatch = createEventDispatcher();

interval = 15;

blocks = [];

createRange = function() {
  var results, runningTime;
  runningTime = calendarDate.clone().beginningOfDay();
  blocks.push(runningTime.clone());
  results = [];
  while (runningTime.isBetween(calendarDate.clone().beginningOfDay(), calendarDate.clone().endOfDay().addMinutes(-15))) {
    runningTime.addMinutes(15);
    results.push(blocks.push(runningTime.clone()));
  }
  return results;
};

createRange();

blockClick = function(block) {
  return console.log(`clicked block ${block}`);
};
</script>
<style lang="sass">table {
  border-spacing: 0;
  border-collapse: collapse; }

td {
  padding: 0px;
