  height: 20px;
  width: 100%;
  width: 30px; }

/*# sourceMappingURL=Day.svelte.css.map */</style>


  <table>
    {#if showColumnHeadings}
    <thead>
      <tr>
        {#if showRowHeadings}
        <th> &nbsp; </th>
        {/if}
        <th> {calendarDate.format("{d}")} </th>
      </tr>
    </thead>
    {/if}
    <tbody>
      {#each blocks as block, index}
      <tr>
        {#if showRowHeadings}
        {#if block.format("{mm}") == "00"}
        <td rowspan=4>{block.format("{HH}:{mm}")}</td>
        {/if}
        {/if}
        <td><button on:click={blockClick(block)}>&nbsp;</button></td>
      </tr>
      {/each}
    </tbody>
    </table>

