<script lang="coffeescript">export let calendarDate = Date.create();
export let events = [];
var current, currentCalendar, currentCalendarHeader, currentMonth, dispatch, eventsForDay, generate, next, nextYear, previous, previousYear, setDay, setWeek, weeks;

import {
  createEventDispatcher
} from 'svelte';

dispatch = createEventDispatcher();

weeks = [];

console.log(events);

eventsForDay = function(day) {
  if (events.length === 0) {
    return [];
  } else {
    return events.filter(function(event) {
      return Date.create(event.start).format("{MM}{dd}") === Date.create(day).format("{MM}{dd}");
    });
  }
};

currentCalendar = [];

currentMonth = null;

currentCalendarHeader = null;

generate = function() {
  var calendarEnd, calendarStart, count, currentWeek, results, runningDate;
  weeks = [];
  if (typeof calendarDate !== "undefined" && calendarDate !== null) {
    currentCalendarHeader = calendarDate.format("{Month} {yyyy}");
    currentMonth = calendarDate.format("{M}");
    calendarStart = calendarDate.clone().beginningOfMonth().beginningOfWeek();
    calendarEnd = calendarDate.clone().endOfMonth().endOfWeek();
    runningDate = calendarStart.clone();
    currentWeek = [];
    count = 0;
    results = [];
    while (runningDate.isBetween(calendarStart, calendarEnd)) {
      currentWeek.push(runningDate.clone());
      runningDate.addDays(1);
      count++;
      if (count === 7) {
        weeks.push(currentWeek);
        currentWeek = [];
        results.push(count = 0);
      } else {
        results.push(void 0);
      }
    }
    return results;
  }
};

current = function() {
  var calendarDate;
  calendarDate = Date.create().clone();
  console.log(calendarDate);
  return generate();
};

next = function() {
  calendarDate.addMonths(1);
  generate();
  return console.log(`calendarDate ${calendarDate}`);
};

previous = function() {
  calendarDate.addMonths(-1);
  generate();
  return console.log(`calendarDate ${calendarDate}`);
};

nextYear = function() {
  calendarDate.addYears(1);
  generate();
  return console.log(`calendarDate ${calendarDate}`);
};

previousYear = function() {
  calendarDate.addYears(-1);
  generate();
  return console.log(`calendarDate ${calendarDate}`);
};

current();

setWeek = function(index) {
  return dispatch("setWeek", weeks[index].first());
};

setDay = function(day) {
  return dispatch("setDay", day);
};
</script>

<style lang="sass">table {
  border: 1px solid gray;
  padding: 14px; }

tr[monthname] td {
  font-weight: bold;
  text-align: center;
  padding: 6px 0px 18px 0px;
  font-size: 18px; }

tr[weekdays] td {
  text-align: center; }

tbody td {
  text-align: center;
  height: 32px;
  width: 32px;
  font-weight: bold; }

[month=previous] {
  color: #dddddd; }

[month=next] {
  color: #dddddd; }

tfoot button {
  padding: 0px;
  background: none;
  border: none; }
  tfoot button:hover {
    color: blue; }

tbody td[events] {
  background: tan; }

tbody td[events="0"] {
  background: none; }

tbody button {
  background: none; }

/*# sourceMappingURL=Month.svelte.css.map */</style>









<table calendar>
  <thead>
    <tr monthname>
      <td colspan="7">{currentCalendarHeader}</td>
    </tr>
    <tr weekdays>
      <td>&nbsp;</td>
      <td>S</td>
      <td>M</td>
      <td>T</td>
      <td>W</td>
      <td>T</td>
      <td>F</td>
      <td>S</td>
    </tr>
  </thead>
  <tbody>
    {#each weeks as week, weekIndex}
      <tr>
        <td>
          <button on:click={setWeek(weekIndex)}>&gt;</button>
        </td>
        {#each week as day, dayindex}
          {#if (parseInt(day.format("{M}")) != parseInt(currentMonth)) }
            {#if (weekIndex < 1) }
              <td month="previous" events={eventsForDay(day).length}><button on:click={setDay(day)}>{day.format("{d}")}</button></td>
            {:else}
              <td month="next" events={eventsForDay(day).length}><button on:click={setDay(day)}>{day.format("{d}")}</button></td>
            {/if}
          {:else}
            <td month="current" events={eventsForDay(day).length}><button on:click={setDay(day)}>{day.format("{d}")}</button></td>
          {/if}
        {/each}
      </tr>
    {/each}
  </tbody>
  <tfoot>
    <tr>
      <td><button type="button" on:click={previousYear}>&laquo;</button></td>
      <td><button type="button" on:click={previous}>&lt;</button></td>
      <td></td>
      <td><button type="button" on:click={current}>&bull;</button></td>
      <td></td>
      <td><button type="button" on:click={next}>&gt;</button></td>
      <td><button type="button" on:click={nextYear}>&raquo;</button></td>
    </tr>
  </tfoot>

</table>
