<script lang="coffeescript">export let item;
var calendarDate, events, panel, saveCb, setDay, setMonth, setPanel, setWeek, update, viewChange;

import uuid from 'uuid';

import {
  databaseLoaded,
  collection
} from "../../../helpers/stores.js";

import Calendar from "../../../components/braintapper/Calendar/Calendar.svelte";

import CalendarSettings from "./_CalendarSettings.svelte";

import Month from "../../../components/braintapper/Calendar/Month.svelte";

import Week from "../../../components/braintapper/Calendar/Week.svelte";

import Day from "../../../components/braintapper/Calendar/Day.svelte";

import List from "../../../components/braintapper/Calendar/List.svelte";

events = item.data.events;

panel = item.data.last_view;

calendarDate = Date.create();

saveCb = function(r) {
  console.log('saved!');
  console.log(r);
};

update = function() {
  collection.save({
    id: item.id,
    data: {
      last_view: panel,
      last_calendar_view_date: Date.create(),
      events: events
    }
  }, saveCb);
};

setPanel = function(toPanel) {
  panel = toPanel;
  item.data.last_view = toPanel;
  return update();
};

viewChange = function(e) {
  console.log("viewchange!");
  panel = e.detail;
  return update();
};

setWeek = function(e) {
  console.log("setWeek");
  console.log(e.detail);
  panel = "week";
  return calendarDate = e.detail;
};

setDay = function(e) {
  console.log("setDay");
  panel = "day";
  console.log(`calendar view ${panel}`);
  console.log(e.detail);
  return calendarDate = e.detail;
};

setMonth = function(e) {
  console.log("setMonth");
  console.log(e.detail);
  panel = "month";
  return calendarDate = e.detail;
};
</script>

