<script lang="coffeescript">var add, click, debouncedRefresh, menuClose, menustate, open, primaryFocus, refresh, update;

import {
  location,
  replace
} from 'svelte-spa-router';

import IconButton from '../../components/common/IconButton.svelte';

import {
  databaseLoaded,
  collection
} from "../../helpers/stores.js";

import CoreIcons from "../../helpers/icons/core.js";

import CommonIcons from "../../helpers/icons/common.js";

import OverflowMenu from "../../components/carbon/OverflowMenu/OverflowMenu.svelte";

import OverflowMenuItem from '../../components/carbon/OverflowMenu/OverflowMenuItem.svelte';

import Tooltip from '../../components/carbon/Tooltip/Tooltip.svelte';

import Search from "../../components/carbon/Search/Search.svelte";

menustate = false;

click = function(target) {
  console.log("location", location);
  return replace(`/${target}`);
};

add = function(type) {
  var cb;
  cb = function(data) {
    return menustate = false;
  };
  return collection.addStub(type, null, cb);
};

refresh = function() {
  return collection.refresh();
};

debouncedRefresh = refresh.debounce(100);

update = function() {
  return debouncedRefresh();
};

primaryFocus = true;

menuClose = function(e) {
  console.log(e.detail.text);
  return add(e.detail.text);
};

open = false; // tooltip
</script>

<style lang="sass">nav {
  padding: 6px 6px 6px 0px;
  height: 48px;
  max-height: 48px;
  width: 100%;
  max-width: 100%;
  overflow: visible; }

input {
  width: 100%; }

label {
  margin-left: 16px; }

[menu] {
  padding: 6px; }

/*# sourceMappingURL=Navigator.svelte.css.map */</style>

<nav layout="row" bg="light-gray">

  <div flex="initial" menu>
    <OverflowMenu on:close={menuClose}>
      <div slot="menu">
        <Tooltip>
          <div slot="trigger">{@html CommonIcons.add }</div>
          Click here to add an item.
        </Tooltip>
      </div>
      <OverflowMenuItem text="note" bind:primaryFocus >
      {@html CoreIcons.note}
      <label>Note</label>
      </OverflowMenuItem>
      <OverflowMenuItem text="markdown">
      {@html CoreIcons.markdown}
      <label>Markdown Doc</label>
      </OverflowMenuItem>
      <OverflowMenuItem text="checklist">
      {@html CoreIcons.checklist}
      <label>Checklist</label>
      </OverflowMenuItem>
      <OverflowMenuItem text="list">
      {@html CoreIcons.list}
      <label>Value List</label>
      </OverflowMenuItem>
      <OverflowMenuItem text="solver">
      {@html CoreIcons.solver}
      <label>Solver</label>
      </OverflowMenuItem>
      <OverflowMenuItem text="code">
      {@html CoreIcons.code}
      <label>Code Snippet</label>
      </OverflowMenuItem>
      <OverflowMenuItem text="timesheet">
      {@html CoreIcons.timesheet}
      <label>Timesheet</label>
      </OverflowMenuItem>
    </OverflowMenu>
  </div>
  <div flex>
    <Search placeholder="Type in text to filter the list" bind:value={collection.filter_string} on:keyup={update} on:change={update} on:input={update}/>
  </div>

</nav>
