<script lang="coffeescript">// note codemirror has some idiosyncratic behaviour on add and removes
// when sorted
export let item;
var checkMode, checkRefreshFlag, codeEditor, comboBoxUpdated, comboBoxValue, currentItemId, debouncedUpdate, forceRefresh, html, inlineEdit, isMounted, lastItemId, saveCb, setMode, shouldFilterItem, showEditor, thisUuid, update;

import uuid from 'uuid';

import {
  onMount,
  afterUpdate,
  onDestroy
} from 'svelte';

import {
  databaseLoaded,
  collection,
  modes
} from "../../../helpers/stores.js";

import ComboBox from "../../../components/carbon/ComboBox/ComboBox.svelte";

thisUuid = `codemirror-${uuid()}`;

comboBoxValue = "";

$: comboBoxItems = modes.modes.map( function (mode) { return { id: mode.name, text: mode.name } } );

inlineEdit = null;

codeEditor = null;

html = item.data.contents;

saveCb = function(r) {};

//console.log "[#{r.id}] saved -- #{r.data.contents}"
//console.log r.data

// cm is a codemirror reference
// because of the dom shifting, it needs to be passed to ensure the correct value
setMode = function() {
  var found;
  console.log(`looking for ${item.data.mode}`);
  found = modes.modes.find(function(mode) {
    return mode.name === item.data.mode;
  });
  console.log("found?");
  console.log(found);
  if (found != null) {
    return modes.load(found.mode, function() {
      console.log(`set mode ${found.mode}`);
      return codeEditor.setOption("mode", found.mode);
    });
  } else {
    return codeEditor.setOption("mode", null);
  }
};

update = function(cm) {
  setMode();
  if (cm != null) {
    if (item.data.contents !== cm.getValue()) {
      item.data.contents = cm.getValue();
      return collection.save({
        id: item.id,
        data: {
          mode: item.data.mode,
          contents: cm.getValue()
        }
      }, saveCb); // mode only
    }
  } else {
    if (codeEditor != null) {
      return collection.save({
        id: item.id,
        data: {
          mode: item.data.mode,
          contents: codeEditor.getValue()
        }
      }, saveCb);
    }
  }
};

debouncedUpdate = update.debounce(500);

forceRefresh = false;

codeEditor = null;

showEditor = function(item) {
  var domElement, elementId;
  elementId = `${item.id}-editor`;
  domElement = document.getElementById(thisUuid);
  if (codeEditor != null) {
    codeEditor.toTextArea();
  }
  codeEditor = CodeMirror.fromTextArea(domElement, {
    lineNumbers: true,
    mode: comboBoxValue,
    readOnly: item.is_locked,
    height: 2e308,
    indentWithTabs: false,
    tabSize: 2,
    indentUnit: 2
  });
  // this is required because of CodeMirror idiosyncracy
  codeEditor.setOption("value", item.data.contents);
  codeEditor.on('focus', function(cm) {
    return console.log("focus");
  });
  codeEditor.on('blur', function(cm) {
    return update(cm);
  });
  return codeEditor.on('change', function(cm) {
    return debouncedUpdate(cm);
  });
};

isMounted = false;

onMount(function() {
  isMounted = true;
  showEditor(item);
  return setMode();
});

// handle property change from parent
$: { if (isMounted) { codeEditor.setOption("readOnly", item.is_locked);  } };

lastItemId = null;

currentItemId = null;

// codemirror has issues on refresh, need to force
// the uuids are not mapped to the item, which is why the uuids change
checkRefreshFlag = function(item) {
  var currentItem;
  if (item.id !== currentItemId) {
    lastItemId = currentItemId;
    currentItemId = item.id;
    currentItem = item;
    if (lastItemId != null) {
      // rebuild the editor with the latest item
      showEditor(item);
      return setMode(); // when null it is the first load
    }
  }
};

onDestroy(function() {
  if (codeEditor != null) {
    // this is required because it will not auto destroy and attach itself to another item in the list
    // what a fucking piece of bullshit
    return codeEditor.toTextArea();
  }
});

checkMode = function(mode) {
  if (mode != null) {
    console.log(`modecheck ${mode}`);
    return update();
  }
};

$: checkRefreshFlag(item);

$: textAreaContents = item.data.contents;

// combobox
shouldFilterItem = function(item, value) {
  if ((value || "").length > 0) {
    //console.log "[#{item.text}].toLowerCase().includes(([#{value}] || '').toLowerCase()) :: #{item.text.toLowerCase().includes((value || "").toLowerCase())}"
    return item.text.toLowerCase().includes((value || "").toLowerCase());
  } else {
    return true;
  }
};

comboBoxUpdated = function(e) {
  console.log("comboBoxUpdate");
  item.data.mode = e.detail.text;
  console.log(item.data.mode);
  return update();
};
</script>

<style lang="sass">[hidden="true"] {
  display: none; }

[mode] {
  margin-top: 24px;
  margin-right: 64px;
  margin-left: 64px; }

label {
  padding-left: 12px;
  font-size: 12px;
  margin-bottom: 12px; }

/*# sourceMappingURL=Code.svelte.css.map */</style>
<div mode>
  <label>Syntax Highlighting Mode</label>
  <ComboBox style="width: 300px; margin-bottom: 24px" size="xl" placeholder="Select a code syntax"  id="combobox-{uuid()}" bind:value={item.data.mode} items={comboBoxItems} {shouldFilterItem} on:change={comboBoxUpdated} />
</div>
<textarea id={thisUuid} bind:value={textAreaContents}/>
